import React, { Component } from 'react';
import { Jumbotron, Button, Container, Row, Col, InputGroup, FormControl, Card, ListGroup, ListGroupItem, ToggleButtonGroup, ToggleButton} from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch } from "@fortawesome/free-solid-svg-icons";
import { Link } from 'react-router-dom';
import axios from 'axios';

// https://codepen.io/Qbrid/pen/GjVvwL?js-preprocessor=none

class Hero extends Component {

  constructor(props) {
    super(props);
    this.state = {
      produtos: [],
      isLoading: false,
      searchText: "",
      male: false,
      female: false
    };
  }

  // getProducts(name) {
  //   const requestOptions = {
  //     method: 'POST',
  //     headers: { 'Content-Type': 'application/json' },
  //     body: JSON.stringify({ 'gender': '', 'name': name })
  //   };
  //   // let url = "http://18.231.80.56:3333/products";
  //   let url = "http://localhost:3333/products";
  //   fetch(url, requestOptions).then(response => response.json())
  //   .then(data => this.setState({ produtos: data, isLoading: false }));
  // }
 
  componentDidMount() {
    this.search();
    // this.getProducts();
    // this.setState({ isLoading: true });
    // const requestOptions = {
    //   method: 'POST',
    //   headers: { 'Content-Type': 'application/json' },
    //   body: JSON.stringify({ 'gender': 'F', 'name': '' })
    // };
    // // let url = "http://18.231.80.56:3333/products";
    // let url = "http://localhost:3333/products";
    // fetch(url, requestOptions).then(response => response.json())
    // .then(data => this.setState({ produtos: data, isLoading: false }));
    // axios.post(`http://localhost:3333/products`, { gender: '', name: '' })
    //   .then(res => {
    //     this.setState({ produtos: res.data, isLoading: false });
    //   })
  }

  search(){
    this.setState({ isLoading: true });
    const { searchText, male, female } = this.state;

    // console.log(male + " - " + female);

    let gender = '';
    if (male) {
      gender = 'M';
    } else if (female) {
      gender = 'F';
    }

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ 'gender': gender, 'name': searchText })
    };
    let url = "http://18.231.80.56:3333/products";
    // let url = "http://localhost:3333/products";
    fetch(url, requestOptions).then(response => response.json())
    .then(data => this.setState({ produtos: data, isLoading: false }));

    // this.props.fetchTermo(termo);
    // Router.push({ pathname: "/pesquisa", query: { termo } });
    // console.log("busca " + termo)
    // let prods = [{"id": 4, "name": "Produto 4"}, {"id": 5, "name": "Produto 5"}, {"id": 6, "name": "Produto 3"} ]
    // this.setState({ produtos: prods, isLoading: false })
  }

  searchByDescription() {
    this.setState({ male: false, female: false }, () => { this.search();});
  }

  searchByMale(){
    this.setState({ male: true, female: false }, () => { this.search();});
  }

  searchByFemale(){
    this.setState({ male: false, female: true }, () => { this.search();});
  }

  render() {
    const { produtos, isLoading } = this.state;
    let content;

    if (isLoading) {
      content = <div className="Carregando">
                <img src={"/loading.gif"} />                
              </div>;
    } else if (produtos.length == 0) {
      // this.setState({ searchText: ""});
      content = <Row className="justify-content-md-center">
                  <Col md="auto">
                    <div style={{ marginTop: '2rem' }}>N√£o encontrou presentes para a busca...</div>
                  </Col>
                </Row>;
    } else {

      content = <Row className="justify-content-md-center">
      {/* <Row class="flex">  */}

      {/* <div class="flex col-lg-3 col-md-6 col-sm-12 col-xs-12"> */}

      {produtos.map(produto =>
      <a href={produto.link} style={{ textDecoration: 'none' }} target="_blank">
        <Card style={{ width: '10rem', margin: '0.5rem' }} key={produto.id}>
        {/* <Card style={{ margin: '0.5rem' }} key={produto.id}> */}
          <Card.Img variant="top" style={{height: '10rem'}} src={produto.img}  />
          <Card.Body>
            <Card.Title style={{ color: '#222222', fontSize: '10px', height: '1.5rem'}}>{produto.name}</Card.Title>
            <Card.Text>
              {/* <span style={{ color: '#222222'}}>{produto.description}</span> */}
              {/* <p style={{ fontWeight: 'bold', color: 'blue', background: 'red'}}>{produto.price}</p> */}
            </Card.Text>
            <Button variant="primary" href={produto.link} target="_blank">{produto.price}</Button>
          </Card.Body>
        </Card>
        </a>
      )}  
      
       </Row>
    }

    return (
      <div className="App">
          <br/>
          <Container style={{minHeight: '30rem'}}>
            {/* <Jumbotron>
                <Row className="justify-content-md-center">
                    <h1>Hello, world!</h1>
                </Row>
            </Jumbotron> */}

            <InputGroup className="mb-3">
                <FormControl 
                  placeholder="Buscar" 
                  aria-label="Buscar" 
                  aria-describedby="basic-addon2" 
                  value={this.state.searchText} 
                  onChange={e => this.setState({ searchText: e.target.value })} 
                  onKeyPress={event => {
                    if (event.key === "Enter") {
                      this.searchByDescription();
                    }
                  }}  
                />
                <InputGroup.Append>
                    <Button variant="primary" onClick={() => this.searchByDescription()} ><FontAwesomeIcon icon={faSearch} /></Button>
                </InputGroup.Append>
            </InputGroup>

            {/* <br/>
            <div className="flex-3 flex flex-center">
                <input
                    name="pesquisa"
                    value={this.state.termo}
                    onChange={e => this.setState({ termo: e.target.value })}
                    placeholder="Digite aqui a sua pesquisa..."
                    className="input-pesquisa" />
                <button
                    onClick={() => this.submitPesquisa()} 
                    className="button-pesquisa">
                    <i className="fa fa-search"></i>
                </button>
            </div> */}

        {/* <br/> */}

        <Row className="justify-content-md-center">
            {/* <Button style={{marginRight: '15px'}} onClick={() => this.submitSearch()} >Namorado</Button>
            <Button style={{marginLeft: '15px', background: '#FF1493', borderColor: '#FF1493'}} onClick={() => this.submitSearch()}>Namorada</Button> */}

            <ToggleButtonGroup type="checkbox">
              <ToggleButton style={{marginLeft: '30px', marginRight: '15px'}} onClick={() => this.searchByMale()} >Namorado</ToggleButton>
            </ToggleButtonGroup>
            <ToggleButtonGroup type="checkbox">
              <ToggleButton style={{marginLeft: '15px', background: '#FF1493', borderColor: '#FF1493'}} onClick={() => this.searchByFemale()}>Namorada</ToggleButton>
            </ToggleButtonGroup>

        </Row>

        {content}

        {/* </div> */}

       

        {/* <div className="Content">
          <Table striped bordered hover>
            <thead className="thead-dark">
              <tr>
                <th>#</th>
                <th>Produto</th>
              </tr>
            </thead>
            <tbody>

              {produtos.map(produto =>
                 <tr key={produto.id}>
                  <td>{produto.id}</td>
                  <td>{produto.name}</td>
                </tr>
              )}             

            </tbody>
          </Table>
        
        </div> */}

        </Container>

      </div>
    )
  };
}

export default Hero;
